import { Component, OnInit } from "@angular/core";
import { ModalController } from "@ionic/angular";
import { SlidesComponent } from "../../components/slides/slides.component";
import { CartModalPage } from "../cart-modal/cart-modal.page";
import { ProductsService } from "./../../services/products.service";
import { BehaviorSubject } from "rxjs";
import { Product } from "../../models/products";

@Component({
	selector: "app-feeds",
	templateUrl: "./feeds.page.html",
	styleUrls: ["./feeds.page.scss"]
})
export class FeedsPage implements OnInit {
	cart = [];
	products = [];
	cartItemCount: BehaviorSubject<number> = null;
	categories = [
		{
			icon: "assets/categories/shirt1.svg",
			type: "Shirt",
			color: "red"
		},
		{
			icon: "assets/categories/hanger.svg",
			type: "Hanger",
			color: "blue"
		},
		{
			icon: "assets/categories/shoes.svg",
			type: "Shoes",
			color: "green"
		},
		{
			icon: "assets/categories/dress.svg",
			type: "Clothings",
			color: "green"
		},
		{
			icon: "assets/categories/bag.svg",
			type: "Bag",
			color: "green"
		},
		{
			icon: "assets/categories/computers.svg",
			type: "Computing",
			color: "blue"
		},
		{
			icon: "assets/categories/mobile.svg",
			type: "Mobile Phones",
			color: "red"
		},
		{
			icon: "assets/categories/game.svg",
			type: "Gaming",
			color: "green"
		}
	];

	constructor(
		private cartService: ProductsService,
		private modalCtrl: ModalController
	) {}

	ngOnInit() {
		this.cart = this.cartService.getCart();
		this.products = this.cartService.getProducts();
		this.cartItemCount = this.cartService.getCartItemCount();
	}

	addToCart(product) {
		this.cartService.addProducts(product);
		console.log(product);
	}

	async openCart() {
		console.log("opening modal");
		const modal = await this.modalCtrl.create({
			component: CartModalPage
		});
		return await modal.present();
	}

	closeModal() {}
}
